*{
    font-family: 'Open Sans', sans-serif;
    margin: 0;
    padding: 0;
}

/* padding text to rigth (a little) pixel perfect */
.Toastify__toast-body{
    padding-left: 10px !important;
}

/* prevent bootstrap button focus */
.btn:focus{
    box-shadow: none !important;
}

.badge {
    margin-right: 10px !important;
    padding-top: 4px !important;
    padding-bottom: 4px !important;
}
#main {
    background-color: #F5F7FA;
    margin-left: 240px;
    padding-top: 70px;
    min-height: 100vh;
}
.navbar {
    background-color: white;
    margin-left: 240px;
    box-shadow: 2px 4px 20px -4px rgba(0,0,0,.1);
}
.navbar h5{
    font-size: 18px;
    margin-bottom: 0;
    font-weight: 400;
    -webkit-transition: color .2s;
    transition: color .2s;
    color: rgba(0,0,0,.5);
}
.navbar h5:hover{
    -webkit-transition: color .2s;
    transition: color .2s;
    color: rgba(0,0,0,.6);
    cursor: pointer;
}
.main-menu {
    background-color: #404E67;
    width: 240px;
    position: fixed;
    overflow-y: auto;
    top: 0;
}

.main-menu > ul {
    list-style: none;
    height: 100vh;
    padding-left: 5px;
    padding-right: 8px;
}

.main-menu .profile {
    margin-top: 30px;
    margin-left: 5px;
    margin-bottom: 10px;
}

.main-menu .profile img {
    width: 95%;
    margin-top: 10px;
}

.main-menu .profile p {
    color: #DCDCDC;
    font-size: 11px;
    margin-top: 20px;
    margin-bottom: 0;
}

.main-menu .profile h6 {
    color: white;
    font-size: 12px;
    margin-top: 3px;
    font-weight: 300;
}

.main-menu > ul .nav-header {
    padding: 24px 15px 8px;
    color: #DCDCDC;
    text-transform: uppercase;
}

.main-menu > ul li a {
    padding: 16px 30px 16px 14px;
    color: #DCDCDC;
    display: block;
    text-decoration: none;
    font-weight: 300;
    font-size: 15px;
    -webkit-transition: background-color .1s;
    transition: background-color .1s;
    display: -webkit-flex;
    display: flex;
    border-radius: 5px;
}

.main-menu > ul li a:hover {
    background-color: rgba(0,0,0,.1);
    -webkit-transition: background-color .1s;
    transition: background-color .1s;
}

.main-menu > ul li .selected {
    background-color: rgba(0,0,0,.15) !important;
    -webkit-transition: background-color .1s;
    transition: background-color .1s;
}

.main-menu > ul li .selected:hover {
    background-color: rgba(0,0,0,.2) !important;
    -webkit-transition: background-color .1s;
    transition: background-color .1s;
}

.main-menu > ul li a i {
    font-size: 20px;
    margin-right: 7px;
    margin-top: 1px;
}

.main-menu > ul > li:last-child {
    margin-bottom: 130px;
}

.main-menu .MenuItemsContainer{
    padding-bottom: 100px;
}
/* cuerpo */
.card-container {
    margin: 0 20px;
}

.card-wrapper {
    background: #fff;
    padding: 20px 25px;
    border-radius: 3px;
    box-shadow: 0 1px 1px rgba(0,0,0,.05);
}

/* header */
.card .header {
    color: #fff;
    background: #4b5366;
    padding: 14px 25px;
    margin: -20px -25px 10px;
    border-radius: 3px 3px 0 0;
}

.card .header h2 {
    margin: 5px 0 0;
    font-size: 24px;
    display: -webkit-flex;
    display: flex;
    font-weight: 600;
}

.card .header h2 span {
    margin-right: 7px;
    font-weight: 400;
}

.card .header .btn {
    font-size: 13px;
    border: none;
    float: right;
    margin-left: 10px;
}

.card .header .btn i {
    float: left;
    font-size: 21px;
    margin-right: 5px;
}

.card .header .btn span {
    margin-top: 2px;
    float: left
}

/* formularios */
.card .form-content{
    margin-top: 25px;
}

/* tablas */
.card table {
   text-align: center;   
   margin-bottom: 0 !important;
}

.card table i {
    cursor: pointer;
    color: #4b5366;
}

.card table i:hover {
    cursor: pointer;
    color: #4b5366;
    -webkit-transition: color .2s;
    transition: color .2s;
}

.card table i:hover {
    color: #ff4842;
    -webkit-transition: color .2s;
    transition: color .2s;
}

.card table thead th{
    font-weight: 600;
}

.card table tbody td{
    font-size: 14px;
    vertical-align: middle;
}

/* filtros */
.table-filter .filter-group {
    float: right;
    margin-left: 15px;
}
.table-filter input, .table-filter select {
    height: 34px;
    border-radius: 3px;
    border-color: #ddd;
    box-shadow: none;
    display: inline-block;
    margin-left: 7px;
}
.table-filter {
    padding: 5px 0 15px;
    margin-bottom: 5px;
}

.table-filter label {
    font-weight: normal;
    margin-left: 10px;
}
.table-filter input {
    width: 250px;
    display: inline-block;
}
.table-filter select {
    margin-right: 7px;
}

/* paginador */
.pagination {
    float: right;
    margin: 0 0 5px;
}
.pagination li span {
    border: none;
    font-size: 13px;
    min-width: 30px;
    min-height: 30px;
    color: #999;
    margin: 0 2px;
    line-height: 30px;
    border-radius: 2px !important;
    text-align: center;
    padding: 0 6px;
}
.pagination li span:hover {
    color: #666;
}	
.pagination li.active span {
    background: #03A9F4;
}
.pagination li.active span:hover {        
    background: #0397d6;
}
.pagination li.disabled i {
    color: #ccc;
}
.pagination li i {
    font-size: 16px;
    padding-top: 6px
}
.hint-text {
    float: left;
    margin-top: 10px;
    font-size: 13px;
}  
.react-autosuggest__container {
  position: relative;
}

.react-autosuggest__input {
  width: 100%;
  height: calc(2.25rem + 2px);
  padding: .375rem .75rem;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: .25rem;
  -webkit-transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.react-autosuggest__input:focus {
    color: #495057;
    background-color: #fff;
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0,123,255,.25);
}


.react-autosuggest__input:disabled {
    background-color: #e9ecef;
    opacity: 1;
}

.react-autosuggest__input--focused {
  outline: none;
}

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  border: 1px solid #aaa;
  background-color: #fff;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  z-index: 2;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 5px 20px;
}

.react-autosuggest__suggestion .details {
  color: gray;
  font-size: 12px;
}

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd;
}

.card-alternative .wrap{
    width: 50%;
    padding: 8% 0 0;
    margin: auto;
    max-width: 500px;
    display: -webkit-flex;
    display: flex;
}

.card-alternative {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #9053c7;
    background: -webkit-linear-gradient(-135deg,#c850c0,#4158d0);
    min-height: 100vh;
}
.card-alternative .card-content {
    position: relative;
    z-index: 1;
    background: #FFFFFF;
    margin: 0 auto 100px;
    padding: 45px;
    text-align: center;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.card-alternative form input {
    outline: 0;
    background: #f2f2f2;
    width: 100%;
    border: 0;
    margin: 0 0 15px;
    padding: 15px;
    box-sizing: border-box;
    font-size: 14px;
    border-radius: 5px;
}
.card-alternative button {
    text-transform: uppercase;
    outline: 0;
    width: 100%;
    border: 0;
    padding: 15px;
    font-size: 14px;
    -webkit-transition: background .2s;
    transition: background .2s;
    cursor: pointer;
    font-weight: 700;
    color: #FFFFFF;
    background: #9053c7;
}
.card-alternative button:hover {
    -webkit-transition: background .2s;
    transition: background .2s;
    background: #6f39a1;
}
